@font-face {
  font-family: LEngineer-Regular;
  src: url(/idealnemoloko-react/static/media/LEngineer-Regular.8a1b91a8.otf);
}
@font-face {
  font-family: Proxima-Nova-Semibold;
  src: url(/idealnemoloko-react/static/media/Proxima-Nova-Regular.410504d4.otf);
}

html {
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
#root {
  height: 100%;
}
#root > div {
  height: 100%;
}

.logo {
  position: absolute;
  top: 5vh;
  left: 11vw;
  margin-left: -3vh;
  height: 14vh;
  z-index: 2;
}

.splash-left {
  position: absolute;
  right: 81vw;
  bottom: -10%;
  height: 85%;
  z-index: 1;
}
.splash-right {
  position: absolute;
  right: 45vw;
  top: -38%;
  height: 80%;
  z-index: 1;
}
.leaves {
  position: absolute;
  right: 57vw;
  bottom: 6vh;
  height: 18vh;
  z-index: 1;
}

.left-bar {
  position: absolute;
  top: 5vh;
  right: 12vw;
  z-index: 2;

  font-family: Proxima-Nova-Semibold;
  font-size: 1.9vh;
  text-shadow: 0px 0px 4px #555555cc;
}
.left-bar > span {
  margin-right: 2vw;
}
.left-bar a {
  text-decoration: none;
  color: #ffe6ab;
}
.left-bar a:hover {
  color: #ffffff;
}

.social {
  position: absolute;
  top: 4.4vh;
  right: 2vw;
  z-index: 2;
}
.social a {
  display: inline-block;
  width: 3vh;
  height: 3vh;
  margin-right: 1.5vw;
}
.social img {
  width: 100%;
  height: 100%;
}
.swiper-container, .swiper-wrapper, .swiper-slide {
  height: 100%;
  z-index: 0;
}

.swiper-slide-right {
  position: absolute;
  right: 0;
  height: 100%;
  width: 57%;
  box-sizing: border-box;

  /* background-image: radial-gradient(circle, #a4df60, #a4df60, #6ac537, #00aa00, #00aa00, #00aa00);
  background-position-x: 4vw;
  background-position-y: 4vh;
  background-repeat: no-repeat; */
}

.swiper-slide-right-title {
  margin-top: 19vh;
  margin-left: 12vw;
  height: 5vh;

  text-align: center;
  font-family: LEngineer-Regular;
  font-size: 3.5vh;
  -webkit-font-smoothing: antialiased;
  color: #7d4526;
}

.swiper-slide-right-img {
  height: 70%;
  text-align: center;
}
.swiper-slide-right-img img {
  position: absolute;
  right: 43%;
  height: 70vh;

  -webkit-transform: translateX(50%);

          transform: translateX(50%);
}

.slide-vivsyane {
  background-color: #ffd889;
}

.slide-grechane {
  background-color: #deb297;
}
.swiper-container-vertical > .swiper-pagination-bullets {
  right: 2vw;
}
.swiper-pagination-bullet {
  height: 2vh;
  width: 2vh;
  background: none;
  border: 0.5vh solid white;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  background: white;
}
.curtain {
  position: absolute;
  left: -48%;
  top: 0;

  width: 100%;
  height: 100%;

  background: #bcffd0;
  /* background-image: radial-gradient(circle, #bcffd0, #b3ffca, #a9ffc4, #9fffbe, #95ffb8); */
  box-shadow: 5px 0px 50px 10px rgba(0,0,0,0.3);
  -webkit-transform: skewX(-10deg);
          transform: skewX(-10deg);
}
.curtain-glow {
  position: absolute;
  right: 25%;
  top: 50%;
  width: 50vw;
  height: 80vh;
  border-radius: 50%;
  box-shadow: 0 0 9vw 6vw #000000, inset 1px 0 9vw 6vw #000000;
  opacity: 0.04;
  -webkit-transform: translate(40%, -50%) skewX(10deg);
          transform: translate(40%, -50%) skewX(10deg);
}
.main {
  position: absolute;
  top: 0;
  left: 11vw;
  width: 35%;
  height: 100%;
  z-index: 2;
}
.main-header {
  margin-top: 24vh;
  height: 6vh;

  font-family: LEngineer-Regular;
  font-size: 3.5vh;
  -webkit-font-smoothing: antialiased;
  font-weight: lighter;
  color: #75321f;
}
.main-body {
  height: 40%;
  margin-top: 4vh;
  margin-right: 2%;
  z-index: 2;

  font-family: Proxima-Nova-Semibold;
  font-size: 1.8vh;
  line-height: 2.7vh;
  color: #266336;
}
